@import "variables";
@import "colors";

.tracker {
	&_login {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		height: calc(100% - 50px);
		&_form {
			width: 320px;
			height: auto;
			&_field {
				display: flex;
				flex-direction: row;
				height: 50px;
				margin-bottom: 15px;
				label {
					width: 50px;
					height: 50px;
					padding: 15px;
					background: $color-header;
					svg {
						fill: $color-text;
						opacity: 50%;
					}
				}
				input {
					width: 270px;
					background: $color-el;
					padding-left: 15px;
					border: none;
					color: $color-text;
				}
			}
			&_submit {
				height: 50px;
				input {
					height: 100%;
					width: 100%;
					background: $color-completed;
					border: none;
					color: black;
					text-transform: uppercase;
					font-weight: 600;
				}
			}
		}
		&_bottom {
			height: auto;
			width: auto;
			margin-top: 15px;
			font-size: 1.3vh;
		}
	}
}

